<template>
    <div class="login_page">
        <!-- <div class="carousel">
            <el-carousel trigger="click" >
            <el-carousel-item  v-for="item in carousels" :key="item">
                <img class="img_carousel" :src="item" alt="asd" srcset="">
            </el-carousel-item>
            </el-carousel>
        </div> -->
        <div class="header">
          <div class="header-left">
            <div>
              <!-- <img src="/static/img/login/logo.png" alt=""> -->
              <img :src="staticImgUrl.logo" alt="">
            </div>
            <!-- <div>
              {{$t('login.platFormName')}}
            </div> -->
          </div>
          <div class="header-right">
            <!-- <el-select v-model="language" placeholder="请选择语言" size="mini" class="longin-language" style="width:120px;text-align:center;" @change="changelanguage">
               <el-option
                  label="中文"
                  value="zh">
                </el-option>
                <el-option
                  label="English"
                  value="en">
                </el-option>
            </el-select> -->
            <span style="margin-left:30px;font-size:12px;color:#252C35;">{{$t('login.homePage')}}</span>
          </div>
        </div>
        <div class="main">
          <div class="login_box">
              <div class="login-title">
                  <label>{{$t('login.accountSub')}}</label>
              </div>
              <div class="login-body">
              <el-form ref="loginForm" :model="form" :rules="rules" label-width="80px">
                <el-form-item label-width ="0" prop="username">
                      <el-input
                      :placeholder="$t('login.pleaseEnterAcc')"
                      v-model="form.username"
                      clearable
                      >
                      </el-input>
                </el-form-item>
                <el-form-item label-width ="0" prop="password">
                      <el-input
                      :placeholder="$t('login.pleaseEnterPass')"
                      v-model="form.password"
                      type="password"
                      clearable
                      @keyup.enter.native="login"
                      >
                      </el-input>
                </el-form-item>
                </el-form>
              </div>
              <div class="login-footer">
                <div style="width:100%;">
                  <el-button type="primary" class="enter" @click="login" style="width:100%;">
                    {{$t('login.submit')}}
                  </el-button>
                </div>
                <!-- <div style="width:100%;display:flex;justify-content:space-between;padding-top:8px;">
                  <el-checkbox v-model="checkedRemember" class="repa">{{$t('login.rememberAcc')}}</el-checkbox>
                  <el-button type="text" style="padding:0;" @click="forgetPassword" :disabled="loadForget">{{$t('login.forgetPass')}}</el-button>
                </div> -->
              </div>
          </div>
        </div>
        <div class="footer">
          <div>
            <!-- <p>Copyright © http://www.koala-vision.com/, All Rights Reserved.</p> -->
            <!-- <p>Email : http://www.koala-vision.com/  QQ: 337489343</p> -->
          </div>
        </div>
    </div>
</template>
<script>
import APICONFIG from '../../../config/api.js'
import treeBuilder from '../../../../static/treeBuilder.js';
import staticImg from '../../../../static/json/imgJson.json';

export default {
  data () {
    return {
      carousels: [],
      checkedRemember: false, //记住密码
      loadForget: false,
      staticImgUrl: staticImg.loginImages[0],
      language: localStorage.language,
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: this.$t('login.pleaseEnterAcc'), trigger: 'blur' },
          { min: 3, max: 40, message: this.$t('login.length1'), trigger: 'blur' }
        ],
        password: [
          { required: true, message: this.$t('login.pleaseEnterPass'), trigger: 'blur' },
          { min: 1, max: 40, message: this.$t('login.length2'), trigger: 'blur' }
        ]
      },
      netApi: null,
      roleArr: [],
    };
  },
  beforeCreate() {
    localStorage.language = 'en'

    if(localStorage.language === 'en') {
      this.$i18n.locale = 'en'
    } else if(localStorage.language === 'zh') {
      this.$i18n.locale = 'zh'
    }
  },
  created () {
    // if (localStorage.getItem('userActive')) {
    //   this.$router.push('/');
    // }
    this.netApi = APICONFIG().get('login');
    this.netAPIRole = APICONFIG().get("role_manage");

    let local = {
      password: localStorage.getItem('password'),
      username: localStorage.getItem('username'),
    }

    if (local.username) {
      this.form = local;
      this.checkedRemember = true
    }

    this.carousels = [
      "https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1534304755&di=ee3dd6abbe30f6c9d60e3e555782bbc4&src=http://a.hiphotos.baidu.com/zhidao/pic/item/cf1b9d16fdfaaf51b3fef0a6805494eef01f7a8d.jpg",
      "https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=759961887,190875110&fm=27&gp=0.jpg",
      "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1534314836898&di=a563d0ca81d3a8c7ab077f1a11fd2975&imgtype=0&src=http%3A%2F%2Fup.enterdesk.com%2Fedpic_source%2F3a%2F68%2Fff%2F3a68ff27363e1b289f69628c7b7f0e0f.jpg"
    ];
  },
  methods: {
    changelanguage(lang) {
      window.location.reload()
      // this.$i18n.locale = lang
      localStorage.language = lang
    },
    login () {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.sendLogin();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    sendLogin () {
      console.log(this.netApi.login)
      this.$http.post(this.netApi.login, this.form).then(
        (res) => {
          if (res.body.status == 200) {
            if (this.checkedRemember) {
              //记住密码
              localStorage.setItem('username', this.form.username);
              localStorage.setItem('password', this.form.password);
            } else {
              //记住密码
              localStorage.removeItem('username');
              localStorage.removeItem('password');
            }
            //登陆状态
            localStorage.setItem('userActive', this.form.username);
            //存储token
            localStorage.setItem('token', res.body.data.token);
            //登陆管理员等级
            localStorage.setItem('role', res.body.data.roleName[0]);
            //获取权限
            // this.getRoleId(res.body.data.roleName[0]);
            // console.log(this.$router.options.routes[1].children)
            // if (!this.$router.options.routes[1].children) {
            //   debugger;
            this.loadRouter();
            // }
          } else {
            this.$message({
              message: res.body.message,
              type: 'warning'
            });
          }
        },
        (err) => {
          this.$message({
            message: this.$t('common.serviceError') + err,
            type: 'warning'
          });
        })
    },
    // getRoleId(roleName) {
    //   this.$http.get(this.netAPIRole.find).then(
    //     res => {
    //       if (res.body.status == 200) {
    //         res.body.data.map(item => {
    //           if(roleName == item.name) {
    //             this.getRole(item.id)
    //           }
    //         })
    //       }
    //     }
    //   );
    // },
    // getRole(id) {
    //   this.$http.get(this.netAPIRole.details + id, { "id": id }).then(
    //     res => {
    //       if (res.body.status == 200) {
    //         console.log(res)
    //         res.body.data.sysMenus.map(item => {
    //           if(item.roleMenuId) {
    //             this.roleArr.push(item)
    //           }
    //         })
    //       }
    //     }
    //   ).then(res => {
    //     localStorage.setItem('roleArr', JSON.stringify(this.roleArr));
    //   });
    // },
    loadRouter () {
      // debugger
      this.$http.get(this.$api().get('jurisdiction').userMenu).then(
        res => {
          if (res.body.status == 200) {
            //目前只有云平台需要添加的路由,把树添加到.options.routes【1】的子节点,main.js根据npm加载的
            //判断是否是已经加载了权限路由
            this.$router.options.routes[1].redirect = { name: '工作台' };
            console.log(res.body.data)
            this.$router.options.routes[1].children = treeBuilder(res.body.data);
            this.$router.addRoutes(this.$router.options.routes);
            this.$router.push('/workbench/workbench');
            // this.$router.push('/login');
            //这里只会纯一个路由的code树
            localStorage.setItem('route', JSON.stringify(res.body.data));
          }
        }
      )
    },
    forgetPassword () {
      if (!this.form.username) {
        this.$message({
          message: '先输入账号才能发送给指定邮箱！',
          type: 'warning'
        });
        return
      }
      this.$http.post(this.netApi.findPass, { username: this.form.username }).then(
        (res) => {
          if (res.body.status == 200) {
            this.$notify({
              title: '邮件发送提醒',
              message: '已给【' + this.form.username + '】用户的邮箱发送邮件，请注意查收！',
              type: 'success'
            });
            this.loadForget = true;
            setTimeout(() => { this.loadForget = false }, 10000);
          } else {
            this.$message({
              message: res.body.message,
              type: 'warning'
            });
          }
        },
        (errer) => {

        })
    },

  }
};
</script>
<style lang="scss" scoped>
.login_page{
  width: 100%;
  height: 100%;
  min-width: 1024px;
  min-height: 768px;
  .header{
    position: relative;
    width: 100%;
    height: 100px;
    min-height: 118px;
    box-sizing: border-box;
    padding-left: 3%;
    padding-right: 9%;
    background: #fff;
    display: flex;
    justify-content: space-between;
    @media screen and(max-width:1700px){
      padding-right: 4.5% !important;
    }
    @media screen and(max-width:1460px){
      padding-right: 1.5% !important;
    }
    @media screen and(max-width:1280px){
      padding-right: 0 !important;
    }
    .header-left{
      width: 350px;
      height: 100%;
      >div{
        width: 50%;
        height: 49px;
        margin-top: 55px;
        &:nth-child(1){
          float: left;
          box-sizing: border-box;
          padding-right: 10px;
          text-align: right;
          img{
            width: 60px;
            height: 49px;
          }
        }
        &:nth-child(2){
          float: right;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 24px;
          font-family: MicrosoftYaHei-Bold;
          font-weight:bold;
          box-sizing: border-box;
          border-left: 2px solid;
        }
      }
    }
    .header-right{
      width: 226px;
      height: 100%;
      box-sizing: border-box;
      padding-top: 75px; 
    }
  }
  .main{
    width: 100%;
    height: 66%;
    background: url('../../../../static/img/login/banner.png') no-repeat;
    // background-size: 100% 100%;
    background-position: center center;
    position: relative;
    .login_box{
        position: absolute;
        top: 25%;
        right: 15%;
        width: 380px;
        height: 310px;
        // min-width: 380px;
        // min-height: 310px;
        box-sizing: border-box;
        padding: 20px;
        padding-top: 5px;
        background: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        @media screen and(max-width:1700px){
          right: 12%;
        }
        @media screen and(max-width:1460px){
          right: 10%;
        }
        @media screen and(max-width:1366px){
          width: 350px;
          height: 285px;
        }
        @media screen and(max-width:1280px){
          width: 320px;
          height: 260px;
        }
        .login-title{
          width: 100%;
          height: 50px;
          font-size: 16px;
          font-family: MicrosoftYaHei;
          display: flex;
          align-items: center;
        }
        .login-body{
          width: 100%;
          height: 150px;
          display: flex;
          flex-direction: column;
          .el-form{
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            padding-top: 15px;
          }
        }
        .login-footer{
          width: 100%;
          flex: 1;
        }
      }
  }
  .footer{
    width: 100%;
    height: 21.6%;
    background: #fff;
    text-align: center;
    >div{
      box-sizing: border-box;
      padding-top: 30px;
      display: inline-block;
      width: 400px;
      height: 34px;
      color: #909399;
      font-family: MicrosoftYaHei;
      font-size: 12px;
      >p{
        margin: 5px 0;
        text-align: left;
      }
    }
  }
}
</style>
<style lang="scss">
.login_page{
  .login-language{
    input{
      border-color: #fff;
      color:#252C35;
    }
  }
}
</style>




